'use client'

import movie from "@/app/movie/page";
import axios from "axios";
import { useEffect, useState } from "react";

interface movieObj{
    _id: number,
    movie_id: number,
    title: string,
    release: number,
    director: string,
    description: string,
    rating: number,
    categories: Array<number>,
     __v: number
}

const API  = ()=>{
    const [movies, setMovies] = useState<Array<movieObj>>([]);
    useEffect(()=>{
        axios.get('http://localhost:8000/movie').then((res)=>{
            // console.log(`Movies : ${JSON.stringify(res)}`);
            setMovies(res.data)
            
        })
        .catch((err)=>{
            console.log(`Error : ${err}`);
            
        })
    });


    return (
        <>
        <h1>APIs </h1>
        <div className="container">
            {movies.map( movie)
            }
        </div>
        </>
    )
};

export default API